#header {
  text-align: center;
}
#left {
  float: left;
  background-color: yellow;
  height: 500px;
  width: 0%;
}
#right {
  float: right;
  background-color: yellow;
  height: 500px;
  width: 0%;
}
#main {
  text-align: center;
}
#footer {
  clear: both;
  background-color: blue;
  height: 100px;
  color: white;
}
.ground {
  width: 13px;
  height: 13px;
  background-color: black;
  float: left;
  border: 1px solid white;
}
.snackbody {
  width: 13px;
  height: 13px;
  background-color: yellow;
  float: left;
  border: 1px solid white;
}
.food {
  width: 13px;
  height: 13px;
  background-color: green;
  float: left;
  border: 1px solid white;
}
#game {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  align-items: flex-start;
}
#scorepanel {
  text-align: left;
  height: 20px;
}
#playground {
  float: left;
  border: 2px solid;
}
#gameDescription {
  text-align: center;
}
.ui.primary.button {
  float: left;

  text-align: center;
}
